@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  font-family: Montserrat,ProximaNova,Arial,Helvetica Neue,sans-serif;
}
.navbar{
  display: flex;
  justify-content: space-between;
}
@media (max-width:600px){
  .navbar{
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .right-section{
    position: absolute;
    z-index: -1;
    opacity: 0.2;
    height: 100%;
    min-width: 100%;
  }
  .image{
    width:100%;
  }
  input{
    background: transparent;
    border:1px solid grey
  }
  .main-container{
    padding-right: 6% !important;
    /* padding-left: 2%; */
  }
  .input-section{
    flex-direction: column;
  }
  .find-food{
    margin-top: 3%;
    padding: 3% 0 !important;
    min-width:60%;
  }
}




@media(max-width:300px){
  .navbar{
    flex-direction: column;
  }
  .right-section{
    position: static;
    position: initial;
    opacity: 1;
  }
}

.left-section{
  display: flex;
  justify-content: space-between;
  margin-bottom: 3%;
}

a{
  text-decoration: none;
  color:#000
}

.login{
  color: #000;
  font-size:16px;
  font-weight:600;
  margin-right:8%
}

.sign-up{
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  font-family: ProximaNova,Arial,Helvetica Neue,sans-serif;
}
.signup-btn{
  background-color: #000;
  box-shadow: 0 1px 3px 0 rgb(0 0 0 / 12%);
  border: none;
  padding:4% 9%
}

.nav-headings{
  font-size: 40px;
  font-weight: 600;
  margin-bottom: 0;
  margin-top: 0;
}

.light-city{
  color: #93959f;
  font-weight: 600;
  font-size: 16px
}

.dark-city{
  font-weight: 600;
  font-size: 16px;
  color: #686b78
}

.find-food{
  width: "30%";
  padding: "2% 5%";
  font-size: 16px;
  font-weight: 700;
  background-color: #fc8019;
  border: none;
  box-shadow: 0 1px 3px 0 rgb(0 0 0 / 12%);
}
.find-food >a{
  color: white;
}

.right-section{
  /* min-width: 50%; */
  max-width: 40%;
}

.nav-citylist li{
  margin-right: 2%;
}
.features-container{
    display:flex;
    justify-content: space-evenly;
    align-items: center;
    background: #2b1e16;
}

@media (max-width:600px){
    .features-container{
        display:flex;
        flex-direction: column;
    background: #2b1e16;
    }
}

.feature-heading{
    font-size: 20px;
    font-weight: 600;
    color: #fff;
    text-align: center;
    line-height: 1.2;
    margin-top: 15%;
    margin-bottom: 5%;
}

.feature-detail{
    font-size: 15px;
    color: #e0cbc1;
    line-height: 1.3;
    font-weight: 300;
}
.restaurant-main{
    display: grid;
    grid-template-columns: 1fr 1.4fr;
}

.left-side{
    padding:20% 15%
}

.right-side{
    display: flex;
}

@media (max-width:600px){
    .right-side{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .left-side{
        padding: 20% 0%;
    }
    .restaurant-main{
        text-align: center;
        display: flex;
        flex-direction: column;
    }
}
.store-links{
    display: flex;
    justify-content: space-evenly;
}
.footer-main{
    background:black;
    padding:5%;
}

.grid{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-column-gap: 10%;
    -webkit-column-gap: 10%;
            column-gap: 10%;
    border-bottom: 1px solid #4d4d4d;
    /* justify-content: space-evenly; */
}

ul{
    list-style: none;
    padding: 0;
}

li{
    line-height: 2;
}

li>a{
    font-size: 17px;
    font-weight: 300;
    color:#ffffff;
}
/* 
a:hover{
    font-weight: 500;
} */

.head{
    color: gray;
    font-size: 15px;
    text-transform: uppercase;
    font-weight: 600;
    /* text-align: center; */
}

.store-link-img{
    display: flex;
    flex-direction: column;
    grid-row-gap: 10%;
    row-gap: 10%;
}

.copyright-footer{
    display: flex;
    align-items:center;
    justify-content: space-between;
    margin-top: 2%;
}

.city-list{
    color:white;
    list-style: none;
}
.cities{
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #4d4d4d;
}

.city-list-container{
    min-width: 20%;
}

@media(max-width:600px){
    *{
        text-align: center;
    }
    .cities{
        flex-direction: column;
        align-items: center;
        
    }
    .grid{
        grid-template-columns: 1fr;
        
    }
    .copyright-footer{
        flex-direction: column;
    }
    .copyright-footer > *{
        margin:3% 0
    }
    .store-link-img img{
        width: 40%;
    }
}

a:hover{
    font-weight: 600;
}
a >img:hover{
    /* padding: 1%; */
    position: relative;
    left:2%;
    top: 5%;
}

